.container{
    position: relative;
    top: 20%;
    display: flex;
    justify-content: right;
    height: 0vh;
    z-index: 15;
    right: 3%;
}

.container__creatersBar{
    width: 10vw;
    min-height: 30vh;
    text-align: right;
}


.container__creatersBar__value{
    font-weight: 300;
    font-size: 18px;
    margin-top: 20px;
    font-family: 'Manrope',sans-serif;
    cursor:pointer;
}

.container__creatersBar__value.selected{
    font-weight: 500;
}

.mobile__container{
    font-weight: 600;
    font-size: 3vw;
    display: flex;
    padding-left: 6.4vw;
    padding-right: 6.4vw;
}

.mobile__container__createrNamer{
    z-index: 15;
    position: relative;
}

.mobile__container__creatersBar{
    display: flex;
    z-index: 15;
    position: relative;
    width: 100vw;
    min-height: 50px;
}
.mobile__container__creatersBar__arrow{
    display: flex;
    align-items: baseline;
    margin-left: 6px;
    margin-top: 3px;
}

.mobile__container__creatersBar__arrow svg{
    transition: transform 0.3s ease-in-out;
    cursor: pointer;
    scale: 220%;
}

.mobile__container__creatersBar__arrow.clicked svg{
    transform: rotate(180deg);
}

.mobile__container__creatersList{
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
    opacity: 0.8;
    width: 100%;
    margin-bottom: 1rem;
    z-index: 6;
    padding: 10px 0px;
    padding-top: 20px;
    left:0;
}


@media (max-width: 1200px) {
    .container__creatersBar__value{
        font-size: 12px;
        font-weight: 300;
    }
}