.container {
    width: 90%;
    height: 100%;
    max-width: 95rem; /* 컨테이너의 최대 너비를 900px로 제한 */
    margin-left: auto;
    margin-right: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr); /* 세 개의 열을 가진 그리드 */
    grid-gap: 23px; /* 동영상 사이의 가로 및 세로 간격을 조정 */
    animation: fadeIn 0.7s ease-in-out;
    position: relative;
    z-index: 5;
    margin-bottom: 10vh;
  }
  
  .container__video {
    /* flex: 0 0 minmax(200px, 300px); */
    height: 0;
    padding-bottom: 63%; /* 동영상 비율에 맞게 높이 조정 */
    position: relative;
    overflow: hidden;
  }

  
  .container__video img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover; /* 이미지 비율을 유지하며 동영상 아이템에 맞춤 */
    filter: grayscale(100%);
    transition: scale 0.5s ease-in-out, filter 1s ease-in-out;
  }

  .container__video img:hover {
    scale:120%;
    filter: grayscale(0%);
  }
  
  .container__video__icon{
    z-index: 50;
    color: white;
    position: absolute;
    width: 30px;
    height: 30px;
    right: 17px;
    top: 17px;
    animation: apper 0.5s;
  }

  .container__video__icon2{
    z-index: 50;
    color: white;
    position: absolute;
    width: 23px;
    height: 23px;
    right: 20px;
    top: 21px;
    animation: apper 0.5s;
  }

  @media (max-width: 1000px) {
    .container {
      grid-template-columns: repeat(2, 1fr); /* 2개의 열을 가진 그리드 */
    }
  }
  
  @media (max-width: 800px) {
    .container {
      grid-template-columns: repeat(1, 1fr);
       /* 1개의 열을 가진 그리드 */
    }
  }
  

  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(30px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

  @keyframes apper {
    0% { transform: scale(0); }
    100% {transform: scale(1); }
  }