.container{
    width: 100%;
    height: 100vh;
}

.wrap{
    width: 100%;
    height: 100%;
    margin: 0 auto;
}


.videoContainer{
    height: 50vh;
    position: relative;
    transition:height 3s ease-in-out;
}

.expand{
    height: 70vh;
}

@media (max-width: 800px) {
    .videoContainer{
        height: 30vh;
        position: relative;
        transition:height 3s ease-in-out;
    }

    .expand{
        height: 70vh;
    }
  }