@keyframes logoDisapper {
    0% {
      opacity:0;
    }
    20%{
        opacity:1;
    }
    90% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }

  @keyframes logoDisapper2 {
    0% {
      opacity:0;
    }
    7%{
        opacity: 0;
    }
    25%{
        opacity:1;
    }
    90% {
      opacity: 1;
    }
    100% {
      opacity: 1;
    }
  }

  @keyframes logoApper {
    0% {
        opacity:0;
      }
      20%{
          opacity:0;
      }
      35% {
        opacity: 0.8;
      }
      100% {
        opacity: 1;
      }
  }

