.Lcontainer{
  position: absolute;
  top: 42%;
  display: flex;
  left: 17vw;
  width: 26vw;
  transform: translate(-50%,-50%);
}

.mobile__container{
  position: absolute;
  top: 42%;
  display: flex;
  right: 0;
  transform: translate(0%,-50%);
  width: 45%;
  color: aliceblue;
  font-size: 2.5vw;
  text-align: center;
}

.Lcontainer__logo{
    opacity: 1;
    /* animation: logoApper 13s ease-in-out; */
    color: aliceblue;
    text-align: left;
}

.Lcontainer__logo{
  opacity: 1;
  /* animation: logoApper 13s ease-in-out; */
  color: aliceblue;
  text-align: center;
}



.Rcontainer__logo{
    cursor: pointer;
    transition:   opacity 0.3s ease-in-out;
    opacity: 0;
    
    animation: logoDisapper 12s ease-in-out;
    color: aliceblue;
    text-align: center;
    font-size: 2vw;
}


.Lcontainer__logo__h{
  font-family: Century Gothic,sans-serif;
  font-size: 2.5vw;
  transition: font-size 0.5s ease-in-out;
}

.mobile__container__logo__h{
  font-size: 4vw;
  font-family: Century Gothic,sans-serif;
  transition: font-size 0.5s ease-in-out;
}



.Rcontainer__logo__h{
    font-family:Century Gothic,sans-serif;

}

.Rcontainer__logo__h3{
    font-family:Century Gothic,sans-serif;
}

.textChanged{
  position: absolute;
  color: rgb(0, 0, 0);
  transition: opacity 0.3s ease-in-out;
  opacity: 1;
  animation: logoApper 12s ease-in-out;
  text-align: center;
  width: 100%;
  height: 25%;
  transform: translate(0%,50%);
}

.textChanged h1{
  font-size: 5vw;
}

.textChanged h3{
font-size: 2vw;
}

.textChanged .mobile__textChanged__h1{
  font-size: 8vw;
}

.textChanged .mobile__textChanged__h3{
  font-size: 4vw;
}

.NotTextChanged{
display: none;
}
