.container{
    width: 100%;
    height: 65vh;
    transition: height 2s ease-in-out;
}

.containeOfMobile{
    width: 100%;
    height: 100vh;
    transition: height 1.5s ease-in-out, translateY 2s ease ease-in-out;
}


.thisOne2{
    width: 100%;
    height: 100%;
    transition: height 2s ease-in-out;
    display: none;
    overflow: scroll;
    position: relative;
    z-index: 5;
}

.firstLoad{
    animation: moveUpDown 5s infinite;
}

.thisOne2::-webkit-scrollbar{
    display: none;
}

.active{
    display:block;
}

.swipe{
    position: relative;
    z-index: 10;
    width: 5rem;
}

.Button__Container{
    position: relative;
    bottom: 80vh;
    height: 18%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    position: relative;
}

.nextPreVideo{
    position: relative;
    width: 100%;
    height: 100%;
    bottom: 65vh;
    object-fit: cover;
    z-index: 3;
    opacity: 0;
}

@media (max-width: 1000px) {
    .Button__Container{
        bottom: 117vh;
        height: 13%;
    }
}

@keyframes moveUpDown{
    0% {
        transform: translateY(0);
      }
      50% {
        transform: translateY(0);
      }
      70% {
        transform: translateY(-60px);
      }
      100% {
        transform: translateY(0);
      }
};

