.container{
    width: 100%;
    height: 18vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 10;
}

.containerForMusicPage{
    position: absolute;
    width: 100%;
    height: 18vh;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10;
    color:white;
}

.container__links{
    text-decoration: none; /* 밑줄 제거 */
    color: inherit; /* 상위 요소의 색상 상속 */
}

.title{
    text-align: center;
}

.container__menu{
    position: absolute;
    right: 6%;
    cursor: pointer;
    scale: 125%;
}

.container__before_bar{
    scale: 125%;
    cursor: pointer;
    position: absolute;
    left: 6%;
}


@media (max-width: 820px) {
    .container {
        width: 100%;
        height: 20vw;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        z-index: 10;
    }
  }