.container{
    width: 100%;
    height: 75vh;
    min-height: 38rem;
    /* min-height: 100vh; */
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 5;
    position: relative;
    top: -30px;
}

.mobile__container{
    width: 100%;
    height: 100%;
    padding-top: 2rem;
    min-height: 38rem;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    position: relative;
    z-index: 5;
    position: relative;
    top: -30px;
}

.container__container{
    width: 30%;
    height: 90%;
    display: flex;
    justify-content: start;
    align-items: center;
    flex-direction: column;
    position: relative;

}

.mobile__container__container{

}

.container__container:hover{
    opacity: 1;
}



.container__container__profile{
    height: 100%;
    width: 100%;
    position: absolute;
    transition: transform 0.5s ease-in-out ;
    /* opacity: 0.6;
    transform-style: preserve-3d;
    transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out; */
}

.container__container__profile.slide{
    transform: translateX(-15vw );
    animation: none;
}


.container__container__profile:hover{
    opacity: 1;
}


.container__container__profile__container{
    cursor: pointer;
    width: 50%;
    height: 100%;
    overflow: scroll;
    max-width: 60rem; /* 컨테이너의 최대 너비를 900px로 제한 */
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    flex-direction: row;
    justify-content: center;
}

.container__container__profile__container::-webkit-scrollbar{
    display: none;
}

.mobile__container__container__profile__container {
    background-color: aliceblue;
    width: 100%;
    height: 20vw;
    overflow: auto;
    /* max-width: 60rem; */
    display: flex;
    flex-wrap: wrap;
    align-content: baseline;
    flex-direction: column;
}



.container__container__detail{
    position: absolute;
    width: 0%;
    height: 100%;
    /* background-color: #a5d2c3; */
    transition: width 0.5s ease-in-out,transform 0.5s ease-in-out;
}

.container__container__detail.slide{
    transform: translateX(18vw);
    width: 199%;
    display: flex;
    justify-content: center;
}




.container__container__detail__img{
    position: relative;
    z-index: 1;
    height: 100%;
    width: 100%;
    cursor: pointer;
}

.container__container__detail__img img{
    position: relative;
    width: 99%;
    height: 100%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    object-fit: cover; /* 이미지가 부모 컨테이너에 꽉 차게(cover) 설정합니다. */
}

.container__container__detail__img img:hover{
    filter: brightness(80%);
}

.container__container__detail__img img:hover + .img__icon {
    display: block;
  }

.img__icon{
    z-index: 50;
    color:white;
    position:absolute;
    width:40px;
    height:40px;
    right: 28px;
    top: 20px;
    animation: apper 0.3s;
}


.album_container{
    margin-top: 12px;
    width: 10vw;
    height: 10vw;
    margin-right: 14px;
    overflow:hidden;
    position: relative;
}

.mobile__album_container{
    width: 15vw;
    height: 15vw;
    margin: 10px;
    position: relative;
}

.album_container img{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%); 
    object-fit: cover;
}

.mobile__album_container img{
    cursor: pointer;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    object-fit: cover;
}


@keyframes blink {
    0% { transform: scale(1); }
    30% { transform: scale(1.05); }
    100% {transform: scale(1); }
  }

  @keyframes apper {
    0% { transform: scale(0); }
    100% {transform: scale(1); }
  }


  @media (max-width: 1200px) {
  .container__container__detail__img{
    position: relative;
    z-index: 1;
    cursor: pointer;
    width: 101vw;
    min-width: calc(100vw - 40px) !important;
    min-height: 67%;
    height: 65vh;
    object-fit: cover;
    margin-left: 20px;
    margin-right: 20px;
  }

  }

