.container {
    display: flex;
    height: 10rem;
    width: 20%;
    align-items: center;
    justify-content: center;
    position: relative;
    animation: lateFadeIn 0.7s ease-in-out;
  }

.container__mobile{
  display: flex;
  width: 89%;
  height: 12%;
  margin-bottom: 2vh;
  border: 0.5px solid black;
}

.container__mobile__line{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
  
  .container__line {
    display: flex;
    width: 150px;
    height: 150px;
    position: relative;
    z-index: 1;
    border-radius: 100%;
    justify-content: center;
    align-items: center;
    transition: all 0.3s ease-in-out;
  }
  
