.container{
    display: flex;
    width: 100%;
    height: 20rem;

    justify-content: center;
    align-items: center;
    margin-top:5vh
}

.Categorycategory__links{
    width: 100%;
    height: 100%;
    /* background-color: antiquewhite; */
    display: flex;
    align-items: center;
    justify-content: center;
}

.mobile__Categorycategory__links{
    width: 100%;
    height: 100%;
    justify-content: center;
    display: flex;
    align-items: center;
}

.Categorycategory__links img{
    width: 8vw;
    height: 2.5vw;
    max-width: 90px;
    max-height: 35px;
}

.mobile__Categorycategory__links img{
    width: 15vw;
    height: 4.5vw;
    max-width: 115px;
    max-height: 40px;
}


@media (max-width: 820px) {
    .container{
        flex-direction: column;
        height: 60%;
        justify-content: center;
        margin-top: 0;
    }
  }