.right_nav_bar {
  position: relative;
  top: 10%;
  display: flex;
  justify-content:right;
  }

.smallWindow_right_nav_bar{
  position: fixed;
  right: 0%;
  top: 0;
  transition: right 0.3s ease-in-out;
  width:100%;
  height: 100vh;
  display: flex;
  z-index: 3;
  }

.smallWindow_right_nav_bar.open{
  z-index: 11;
}


  .isWindowSmall__container{
    display: flex;
    width: 100%;
    justify-content: right;
    height: 100%;
  }


  .OutOfMenubar{
    position: fixed;
    width: 100%;
    height: 100%;
    background-color:rgb(255,255,255);
    opacity:0.2;
    display: none;
    z-index: 11;
  }

  
  
  .OutOfMenubar.open{
    display: block;
    z-index: 11;
  }
  
  .menu_items {
    z-index: 3;
    width: 100px;
    right: 0;
    top: 30%;
    padding: 10px;
    position: fixed;
    margin-right: 3%;
}

.menu_items.none{
  position: absolute;
}

.menu_items.opens{
  left:20%;
}
  

  .menu_items li{
    list-style-type: none;
    margin-bottom: 30px;
    transition: scale 0.3s ease-in-out;
  }

  .menu_items li:hover{
    scale: 110%;
  }

  .menu_items a{
    text-decoration: none;
    color:rgb(0, 0, 0);
  }


  .menu_items a.open{
    color:white;
  }

  
  
  .menubar{
    position: absolute;
    right: -45%;
    width: 25%;
    background-color: rgba(12,12,12,.8);
    display: flex;
    justify-content: center;
    align-items: center;
  }

  @media (max-width: 1000px) {
    .menubar {
      width: 45%;
    }
  }

  .menubar.positioning{
    position: relative;
    z-index: 12;
  }

  .menubar.slide.positioning {
    transition: right 0.3s ease-out; 
    right: 0%;  
  }



