

  *,
  :before,
  :after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  .style{
    position: relative;
    z-index: 10;
}

  .container {
    width: 50px;
    height: 50px;
    position: relative;
    border-radius: 50%;
    z-index: 10;
  }

  .play_btn {
    position: absolute;
    appearance: none;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background: conic-gradient(#ffffff, #ffffff);
    cursor: pointer;
    outline: none;
  }

  .play_btn::before {
    content: "";
    position: absolute;
    width: 93%;
    height: 93%;
    background-color: #000;
    border-radius: 50%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  .play_btn:checked {
    animation: borderAnimate 700ms ease-in-out 1;
    animation-fill-mode: forwards;
  }

  @keyframes borderAnimate {
    0% {
      transform: rotate(0);
      background: conic-gradient(#ffffff, transparent 20%);
    }

    80% {
      background: conic-gradient(#ffffff, transparent 90%);
    }

    100% {
      transform: rotate(360deg);
      background: conic-gradient(#ffffff, #ffffff);
    }
  }

  .play_icon {
    position: absolute;
    width: 20px;
    height: 20px;
    left: 60%;
    top: 50%;
    background-color: #ffffff;
    transform: translate(-60%, -50%) rotate(90deg);
    clip-path: polygon(50% 15%, 0% 100%, 100% 100%);
    transition: all 400ms ease-in-out;
    cursor: pointer;
  }

  .play_btn:checked + .play_icon {
    clip-path: polygon(0 100%, 0% 100%, 100% 100%);
  }

  .pause_icon {
    position: absolute;
    width: 15px;
    height: 20px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    cursor: pointer;
  }

  .pause_icon::before {
    content: "";
    position: absolute;
    width: 0%;
    height: 100%;
    background-color: #ffffff;
    left: 0;
  }

  .pause_icon::after {
    content: "";
    position: absolute;
    width: 0;
    height: 100%;
    background-color: #ffffff;
    right: 0;
  }

  .play_btn:checked ~ .pause_icon::before {
    animation: reveal 300ms ease-in-out 350ms 1;
    animation-fill-mode: forwards;
  }

  .play_btn:checked ~ .pause_icon::after {
    animation: reveal 300ms ease-in-out 600ms 1;
    animation-fill-mode: forwards;
  }

  @keyframes reveal {
    0% {
      width: 0;
    }

    100% {
      width: 35%;
    }
  }